.hero--container {
  margin-top: 2em;
  padding: 1em;
  display: grid;
  grid-template-columns: 1fr;
  text-align: center;
}

.hero--container:nth-child(odd) h2,
.hero--container:nth-child(odd) .division,
.hero--container:nth-child(odd) .info--container {
  background-color: #00ff4a;
}

.hero--container:nth-child(even) h2,
.hero--container:nth-child(even) .division,
.hero--container:nth-child(even) .info--container {
  background-color: #ff00b5;
}

h2 {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Bangers', cursive;
  font-size: clamp(1.5rem, 4.5vw, 2.5rem);
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 0.05rem;
}

h3 {
  font-size: clamp(1rem, 4.5vw, 1.5rem);
  font-weight: 600;
  letter-spacing: 0.05rem;
  margin-bottom: 0;
}

p {
  font-size: 1rem;
  font-weight: 500;
  letter-spacing: 0.05rem;
  margin: 1em;
}

.division {
  width: 100%;
  height: 30px;
}

h2,
.info--container,
.division {
  border: 6px solid #000000;
  box-sizing: border-box;
  box-shadow: 8px 8px 2px #000000;
}
.image--container {
  margin: 2em 0;
}
img {
  /* box-shadow: 9px 9px 4px #000000; */
  border: 4px solid black;
  outline-offset: -13px;
  outline: 10px solid white;
}

.card-powerstats {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 1em;
}

.powerstat {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  text-transform: capitalize;
  padding: 0.25em;
  align-items: left;
  color: black;
}
/* background-color: aqua; */

.powerstat--name {
  margin-left: 1em;
}

.powerstat--container {
  /* border: 2px solid black; */
  background: rgba(231, 226, 226, 0.5);
  width: 60%;
  padding: 0;
  margin-right: 1em;

  /*  */
}

.black {
  background-color: black;
  padding: 0.5em;
  animation: loading 5s linear normal;
}

@media screen and (min-width: 850px) {
  .hero--container {
    grid-template-columns: repeat(4, 200);
    gap: 1.5em;
  }
  h2 {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row: 1;
  }

  .image--container {
    margin: 2em;
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row: 1/3;
  }

  .division {
    grid-column: 1/3;
    grid-row: 3/4;
  }
  .info--container {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row: 2/3;
  }
}

/* animations */
@keyframes loading {
  0% {
    width: 0;
  }
  100% {
    width: inherit;
  }
}
