.hero {
  position: relative;
  min-height: 70vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-image: radial-gradient(#476ce7 20%, transparent 20%),
    radial-gradient(#fafafa 20%, transparent 20%);
  background-color: #a6e0e7;
  background-position: 25px 25px, 100px 100px;
  background-size: 10px 10px;
}

.clouds {
  position: absolute;
  bottom: -10px;
}

